.overlay {
  background: $black;
}

.myLibraryButtons {
  padding-top: 49px;
  padding-bottom: 65px;
  display: flex;
  justify-content: center;
}

.btn-lib {
  width: 130px;
  height: 44px;
  color: $white;

  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px;
  background: transparent;
  transition: background-color, transform, color 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: $accent-icon;
    transform: scale(1.15);
    color: $black;
  }
  &:not(:last-child) {
    margin-right: 20px;
  }
}
.active {
  background-color: $accent-icon;
}

.js-message {
  text-align: center;
}
