//Gallery Films
.movies {
  padding-top: 20px;
  @media screen and (min-width: 768px) {
    padding-top: 30px;
  }
}
.gallery {
    @media screen and (min-width: $tablet) {
    display: flex;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -30px;
  }
//   @include flexBox($w: wrap, $jc: start);
}

.movie-card {
  @include for-size(phone) {
    margin: 15px;
  }
  @include for-size(tablet) {
    width: calc((100% - 4 * 15px) / 2);
    margin: 15px;
  }
  @include for-size(desktop) {
    width: calc((100% - 6 * 15px) / 3);
    transition: box-shadow 0.4s cubic-bezier(.22,.64,.83,.67),
    transform 0.5s cubic-bezier(.52,-0.06,1,1);
  &:hover,
  &:focus {
    transform: scale(1.02);
}
    margin: 15px;
  }
}
.movie-image {
  border-radius: 5px;
  margin-bottom: 10px;

  @include for-size(phone) {
    width: 280px;
  }
  @include for-size(tablet) {
    width: 294px;
    height: 398px;
  }
  @include for-size(desktop) {
    width: 274px;
    height: 398px;
  }
  &:hover {
    transition: 0.25s;
    transform: scale(1.05);
    cursor: pointer;
  }
}

.container {
  @include for-size(phone) {
    width: 280px;
  }
  @include for-size(tablet) {
    width: 618px;
  }
  @include for-size(desktop) {
    width: 882px;
  }
}
.movie-title {
  // width: 113px;
  height: 31px;

  font-style: normal;
  font-weight: 500;

  text-transform: uppercase;

  color: #000000;
  font-family: Roboto;
  font-size: 12px;

  line-height: 16px;
  letter-spacing: 0em;
  text-align: left;
}

.genre {
  color: $accent-text;
  margin-right: 3px;
}
.genre:not(:first-child) {
  margin-left: 3px;
}
.movie-release-date {
  color: $accent-text;
  margin-right: 3px;
}
.movie-release-date::before {
  content: '|';
}

.genre:first-child::after {
  content: ', ';
  color: #ff6b08;
}

.movie-vote {
  color: $white;
  background-color: $accent-icon;
  margin-left: 10px;
  padding: 2px 9px 1px;
  border-radius: 5px;
}

.genre-release-date {
  @include flexBox($w: nowrap, $jc: start);
  font-size: 12px;
}
